



























































































































import { Vue, Component, Watch } from "vue-property-decorator";
import { supplyApi } from "@oa/api";
import { suppliesIn } from "@oa/api/supplies";
@Component({})
export default class PushInPage extends Vue {
	orderState: string = "";
	supplyList: any = [];
	model: any = {
		amount: "",
		unit: "",
		name: "",
		operatorName: "",
		operateTime: "",
		specification: "",
		relateUrl: "",
	};

	SceneList2: any = [
		{
			name: "工程管理类",
			children: [
				{
					name: "污水处理及配套设施",
					val: `11`,
					children: [
						{
							name: "1类项目",
							val: `962`,
						},
						{
							name: "2类项目",
							val: `4186`,
						},
					],
				},

				{
					name: "饮用水源地污染防治项目",
					val: `12`,
					children: [
						{
							name: "地表水治理",
							val: `830`,
						},
						{
							name: "山泉水饮用",
							val: `213`,
							children: [
								{
									name: "开发",
									val: `213-1`,
								},
								{
									name: "环境维护",
									val: `213-2`,
								},
							],
						},
					],
				},
				{
					name: "区域水环境综合整治",
					val: `13`,
				},
			],
		},
		{
			name: "管理类项目",
			children: [
				{
					name: "土地管理",
					val: `21`,
					children: [
						{
							name: "土地面测绘",
							val: `21-1`,
						},
						{
							name: "土地资源整合",
							val: `21-2`,
						},
					],
				},
				{
					name: "环境管理",
					val: `22`,
				},
				{
					name: "电力管理",
					val: `23`,
				},
			],
		},
	];
	estateId: string = "";

	orderStateList: any = [
		{
			id: "0",
			name: "待审核",
		},
		{
			id: "1",
			name: "待接单",
		},
	];

	get isOk() {
		if (
			this.model.name &&
			this.model.operatorName &&
			this.model.relateUrl &&
			this.model.operateTime &&
			this.model.amount
		) {
			if (this.numberCheck()) {
				return true;
			}
		} else {
			return false;
		}
	}

	numberCheck() {
		const reg = /^\d{1,}$/;
		return reg.test(this.model.amount);
	}

	async getSupplyName() {
		const params = {
			name: this.model.name,
		};
		try {
			const res = await supplyApi.getSupplyName(params);
			this.supplyList = res.data;
			console.log(this.supplyList);
		} catch (error) {
			console.log(error);
		}
	}

	orderStateSearch() {}
	async submit() {
		console.log(this.model);

		this.model.operateTime = this.$utils.dateFormat(
			this.model.operateTime,
			"yyyy-MM-dd HH:mm:ss"
		);
		const params = this.model;
		try {
			const res = await supplyApi.suppliesIn(params);
			this.$message({
                message: res.message=='success'?'入库成功!':res.message,
                type:'success'
			});
		} catch (error) {
			this.$message({
				message: error.data.message,
			});
		}
	}

	mounted() {
		this.$nextTick(() => {
			this.getSupplyName();
		});
	}
}
